#main-container{
    background-color: white;
    margin: 0px;
    width: 100% !important;
    max-width: 100% !important;
    border-radius: 10px;
    border: 2px solid rgb(7, 7, 7);
    color: black;
}

.navlink-button{
    padding: 0px;
    margin: auto 10px;
}

.navbar-brand{
    margin-right: 0px;
}

#navlink-request{
    position: absolute;
    right: 0px;
}

a{
    line-height:16px;
    color: #555555;
}

a:hover{
    color:#aaaaaa;
    text-decoration: none;
}

.navlink-selected{
    color:#00bfff;
}

.btn-outline-primary{
    border-color:#00bfff;
}